import * as React from 'react';
import Button from '@mui/material/Button';
import CssBaseline from '@mui/material/CssBaseline';
import TextField from '@mui/material/TextField';
import Box from '@mui/material/Box';
import Container from '@mui/material/Container';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import {Typography} from "@mui/material";
import {LoginType, SpaceType} from "../lib/interface/local";
import {errorNotice, successNotice} from "./notice";
import {loginRequest} from "../lib/api/api";
import {addLoading, delLoading} from "./loading";
import {useEffect, useState} from "react";

const theme = createTheme();

interface Props {
  spaceID: number
  spaceList?: SpaceType[]
  onSpaceChange: (data: SpaceType[]) => void
}
export default function Index(props: Props) {
  const [url, setUrl] = useState('')
  const [account, setAccount] = useState('')
  useEffect(() => {
    if(props.spaceList === undefined
      || props.spaceList.length - 1 < props.spaceID) {
      errorNotice('')
      return
    }
    const login = props.spaceList[props.spaceID].login
    setUrl(login.url)
    setAccount(login.account)
  }, [])

  const handleSubmit = async (event: React.FormEvent<HTMLFormElement>) => {
    event.preventDefault();
    const data = new FormData(event.currentTarget);
    addLoading()
    await login(data)
    delLoading()
  }

  const login = async (data: FormData) => {
    let res = { token: ''}
    try {
      res = await loginRequest({
        url: data.get('url') as string,
        account: data.get('account') as string,
        password: data.get('password') as string,
      })
      successNotice('')
    } catch (err) {
      errorNotice(err as string)
      return
    }

    const newLogin: LoginType = {
      url: data.get('url') as string,
      account: data.get('account') as string,
      token: res.token
    }
    if(props.spaceList === undefined || props.spaceList.length - 1 < props.spaceID) {
      errorNotice('')
      return
    }
    const newData = [...props.spaceList]
    newData[props.spaceID].login = newLogin
    props.onSpaceChange(newData)
  }

  const handleUrlChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    setUrl(event.target.value as string);
  };
  const handleAccountChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    setAccount(event.target.value as string);
  };


  return (
    <ThemeProvider theme={theme}>
      <Container component="main" maxWidth="xs" sx={{
        position: 'absolute',
        top: '50%',
        left: '50%',
        transform: 'translate(-50%, -50%)',
        width: 600,
        bgcolor: 'background.paper',
        border: '2px solid #000',
        boxShadow: 24,
        outline: 0
      }}>
        <CssBaseline />
        <Box
          sx={{
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
            padding: 4,
            background: '#ffffff',
          }}
        >
          <Typography component="h1" variant="h5">
            
          </Typography>
          <Box component="form" onSubmit={handleSubmit} noValidate sx={{ mt: 1 }}>
            <TextField
              margin="normal"
              required
              fullWidth
              id="url"
              label=""
              name="url"
              value={url}
              onChange={handleUrlChange}
              autoFocus
            />
            <TextField
              margin="normal"
              required
              fullWidth
              id="account"
              label=""
              value={account}
              onChange={handleAccountChange}
              name="account"
            />
            <TextField
              margin="normal"
              required
              fullWidth
              name="password"
              label=""
              type="password"
              id="password"
              autoComplete="current-password"
            />
            <Button
              type="submit"
              fullWidth
              variant="contained"
              sx={{ mt: 3, mb: 2 }}
            >
              
            </Button>
          </Box>
        </Box>
      </Container>
    </ThemeProvider>
  );
}